<template>
    <div>
        <div class="panel panel-info">
            <div class="panel-body">
                <div>用户管理</div>
                <div class="buttonbox" v-show="!inforadmin">
                     <div class="col-sm-1" v-if="hasLoginComponents('sys:userMan:userOpera')">
                        <select class="form-control" v-model="auditData.id" @change="changeAudit">
                            <option value="0">未审核</option>
                            <option value="1">已审核</option>
                        </select>
                    </div>
                    <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="hasLoginComponents('sys:userMan:roleOpera')">审核</button>
                    <button type="button" class="btn btn-warning" @click="updateData" v-if="hasLoginComponents('sys:userMan:roleOpera')">退审</button>
                    <button type="button" class="btn btn-danger" @click="deleteData" v-if="hasLoginComponents('sys:userMan:userOpera')">删除</button>
                    <div class="pull-right col-sm-3 search-input" v-if="hasLoginComponents('sys:userMan:search')">
                        <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                        <input type="text" class="form-control" placeholder="搜索用户"> 
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                        </div>
                    </div>
                </div>
                <tables v-show="!inforadmin" :statetitle="statetitle" :state="stateid" @tableCheck="onTableClick" :pageloginData="pageloginData"></tables>
            </div>
        </div>
    </div>
</template>
<script>
import tables from "@/components/table.vue";
import addInfor from "@/components/addinfor.vue";
export default {
  data:function(){
    return {
        checkData:[],
        inforadmin:false,
        searchData:{data:""},
        auditData:{id:0},
        pageloginData:{
            id:this.stateid,
        }
    };
  },
 props:{
     stateid:String,
     statetitle:String
 },
  components: {
    tables,
    addInfor
  },
  methods:{
    onTableClick(data){
        this.checkData=data;
    },
      //判断是否当前页面是否存在按钮权限
   hasLoginComponents(perm){
      var vm=this;
       return vm.$api.opreate.hasLoginComponents(vm,perm);
    },
    inforAddClick(bool) {
       var vm = this;
      if (vm.$api.opreate.TESTcheck(vm,'审核')) {
          
      }
    },
    // 修改按钮按下
    updateData() {
      var vm = this;
      if (vm.$api.opreate.TESTcheck(vm,'退审')) {
           this.inforadmin=true;   
      }
    },
    // 删除按钮按下
    deleteData() {
        var vm = this;
        var checkData=vm.checkData;
      if (vm.$api.opreate.TESTcheck(vm,'删除'))  {
        var id = vm.$layer.confirm(
          '<div class="layer divtext"><i class="icon icon-question"></i>是否删除</div>',
          () => {
            vm.$layer.close(id);
            var datas = {
              id: checkData[0].id,
              states: {
                id: vm.stateId
              }
            };
            vm.$axios
              .post(vm.HOST + "deleteData.action", datas)
              .then(Response => {
                var res = Response.data;
                if (res.code == "0") {
                  vm.$layer.alert(
                    '<div class="layer divtext"><i class="icon icon-success"></i>删除成功</div>'
                  );
                } else {
                  vm.$layer.alert(
                    '<div class="layer divtext"><i class="icon icon-error"></i>' +
                      res.msg +
                      "</div>"
                  );
                }
              })
              .catch(error => {
                vm.$layer.alert(
                  '<div class="layer divtext"><i class="icon icon-warning"></i>网络错误，请检查网络是否畅通</div>'
                );
              });
          }
        );
      }
    },
    //搜索按钮按下
    searchBtnClick(){
      var vm=this;
      if(vm.searchData.data!=""){

      }else{
         vm.$layer.alert(
          '<div class="layer divtext"><i class="icon icon-warning"></i>请输入用户名后搜索</div>'
        );
      }
    },
    //调换审核状态查看
    changeAudit() {
      var vm = this;
      var data = vm.auditData;
      vm.$axios
        .post("", data)
        .then(success => {
          console.log(success.data);
          var res=success.data;
          if(res.code=='0'){

          }else{

          }
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  watch:{
      stateid(news){
         this.pageloginData.id=news;
      }
  }
};
</script>

<style>
</style>



